
import { uniq } from 'lodash'

export default {
  layout: 'blog',
  async asyncData ({ $content, params, redirect }) {
    const posts = await $content('blog')
      .only(['slug', 'cover', 'title', 'author', 'created', 'tags', 'excerpt'])
      .sortBy('created', 'desc')
      .fetch()

    const tags = uniq(posts.flatMap(post => post.tags))

    return { posts, tags }
  },
  head () {
    return {
      title: '部落格'
    }
  }
}
